import { React, useState, useEffect } from "react";
import Layout from "../layout/layout";
import Select from "react-tailwindcss-select";
import { useForm } from "react-hook-form";
import axios from "axios";

export default function Interest() {
  const { register, handleSubmit } = useForm();
  const [formSubmitted, setFormSubmitted] = useState(false);

  useEffect(() => {
    if (formSubmitted) {
      const timer = setTimeout(() => {
        window.location.href = "/";
      }, 3000);

      return () => clearTimeout(timer);
    }
  }, [formSubmitted]);

  const onSubmit = (data) => {
    const payload = {
      email_address: data.Email,
      status: "subscribed",
      merge_fields: {
        FNAME: data.firstName,
        LNAME: data.lastName,
        USERTYPE: data.type,
      },
    };

    // Set the Mailchimp API key as an authentication header
    const apiKey = "a9b4ead80f84df50339ef01828daff5e-us21";
    const authHeader = `Bearer ${apiKey}`;

    // Make the HTTP request to Mailchimp API with the authentication header
    axios
      .post(
        "https://us21.api.mailchimp.com/3.0/lists/66a419e90e/members",
        payload,
        {
          headers: {
            Authorization: authHeader,
          },
        }
      )
      .then((response) => {
        console.log("Form data sent to Mailchimp successfully:", response.data);
        // Add any additional logic or redirect after successful submission
        formSubmitted(true);
      })
      .catch((error) => {
        console.error("Error sending form data to Mailchimp:", error);
        // Handle any error or display an error message to the user
      });
  };

  return (
    <Layout page="Register">
      <div className="my-18 py-36 w-screen bg-white px-6 h-[920px]">
        <div className="mx-auto max-w-2xl text-center">
          <h2 className="text-3xl font-bold tracking-tight text-gray-900 sm:text-4xl">
            Register your interest
          </h2>
          <p className="mt-2 text-lg leading-8 text-gray-600">
            Join our growing database of artists, venues, investors and more who
            want to receive updates about {register}
            <span className="text-primary-200 font-semibold">Otis</span>.
          </p>
        </div>
        <link
          href="//cdn-images.mailchimp.com/embedcode/classic-071822.css"
          rel="stylesheet"
          type="text/css"
        />
        <form
          action="https://otisentertainment.us21.list-manage.com/subscribe/post?u=fca1542e53a6223ea7a86e6d6&amp;id=66a419e90e&amp;f_id=00b95ae1f0"
          method="post"
          id="mc-embedded-subscribe-form"
          name="mc-embedded-subscribe-form"
          className="validate mx-auto mt-16 max-w-xl sm:mt-20"
          target="_blank"
          noValidate
        >
          <div className="grid grid-cols-1 gap-y-6 gap-x-8 sm:grid-cols-2">
            <div>
              <label
                htmlFor="first-name"
                className="block text-sm font-semibold leading-6 text-gray-900"
              >
                First name <span className="asterisk">*</span>
              </label>
              <div className="mt-2.5">
                <input
                  type="text"
                  placeholder="John"
                  name="FNAME"
                  className="required block w-full rounded-md border-0 py-2 px-3.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-300 hover:ring-primary-300 sm:text-sm sm:leading-6"
                  id="mce-FNAME"
                />
              </div>
            </div>
            <div>
              <label
                htmlFor="last-name"
                className="block text-sm font-semibold leading-6 text-gray-900"
              >
                Last name <span className="asterisk">*</span>
              </label>
              <div className="mt-2.5">
                <input
                  type="text"
                  placeholder="Smith"
                  name="LNAME"
                  className="required block w-full rounded-md border-0 py-2 px-3.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-300 hover:ring-primary-300 sm:text-sm sm:leading-6"
                  id="mce-LNAME"
                />
              </div>
            </div>
            <div className="sm:col-span-2">
              <label
                htmlFor="mce"
                className="block text-sm font-semibold leading-6 text-gray-900"
              >
                Email <span className="asterisk">*</span>
              </label>
              <div className="mt-2.5">
                <input
                  type="email"
                  placeholder="johnsmith@mail.com"
                  name="EMAIL"
                  className="required email block w-full rounded-md border-0 py-2 px-3.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-300 hover:ring-primary-300 sm:text-sm sm:leading-6"
                  id="mce-EMAIL"
                  required
                />
              </div>
            </div>
            <div className="sm:col-span-2">
              <label
                htmlFor="mce-PHONE"
                className="block text-sm font-semibold leading-6 text-gray-900"
              >
                Phone number
              </label>
              <input
                type="number"
                placeholder="0412 345 678"
                name="PHONE"
                className="block w-full rounded-md border-0 py-2 px-3.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-300 hover:ring-primary-300 sm:text-sm sm:leading-6"
                id="mce-PHONE"
              />
            </div>

            <div className="sm:col-span-2">
              <label className="block text-sm font-semibold leading-6 text-gray-900">
                What best describes you?
              </label>
              <div className="mt-2.5">
                <select
                  name="USERTYPE"
                  class="required email block w-full rounded-md border-0 py-2 px-3.5 text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-primary-300 hover:ring-primary-300 sm:text-sm sm:leading-6"
                  id="mce-USERTYPE"
                >
                  <option value=""></option>
                  <option value="I'm an artist">I'm an artist</option>
                  <option value="I'm interested in finding artists">
                    I'm interested in finding artists
                  </option>
                  <option value="I'm interested in investing">
                    I'm interested in investing
                  </option>
                  <option value="I'm interested">I'm interested</option>
                </select>
                <span id="mce-USERTYPE-HELPERTEXT" class="helper_text"></span>
                <div id="mce-responses" className="clear foot">
                  <div
                    className="response"
                    id="mce-error-response"
                    style={{ display: "none" }}
                  ></div>
                  <div
                    className="response"
                    id="mce-success-response"
                    style={{ display: "none" }}
                  ></div>
                </div>
                <div
                  style={{ position: "absolute", left: "-5000px" }}
                  aria-hidden="true"
                >
                  <input
                    type="text"
                    name="b_fca1542e53a6223ea7a86e6d6_66a419e90e"
                    tabIndex="-1"
                    value=""
                  />
                </div>
              </div>
            </div>
          </div>
          <div className="mt-10">
            <input
              type="submit"
              value="Subscribe"
              name="subscribe"
              id=""
              className="block w-full rounded-md bg-primary-300 px-3.5 py-2.5 text-center text-sm font-semibold text-white shadow-sm hover:bg-primary-400 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-primary-300"
            ></input>
          </div>
        </form>
        {formSubmitted && (
          <div className="fixed top-0 left-0 w-full h-full bg-gray-900 bg-opacity-50 flex items-center justify-center">
            <div className="bg-white p-8 rounded shadow">
              <p>
                Thank you for registering your interest. You will now be
                redirected to the homepage.
              </p>
            </div>
          </div>
        )}
      </div>
    </Layout>
  );
}

const options = [
  { value: "artist", label: "I'm an artist" },
  { value: "venue", label: "Interested in finding artists" },
  { value: "investor", label: "Interested in investing" },
  { value: "undefined", label: "General interest" },
];

const MultiDropdown = () => {
  const [type, setType] = useState(null);

  const handleChange = (value) => {
    console.log("value:", value);
    setType(value);
  };

  return (
    <Select
      value={type}
      onChange={handleChange}
      options={options}
      isMultiple={true}
    />
  );
};

